import "./index.less";
import React, { Component } from "react";

import { getData, getQuery } from "../../../lib/app/js/app";
import { PORTOCAL } from "../../../lib/app/js/env";

export default class IcoPublish extends Component {
  render() {
    let { unit } = this.props;
    let info = this.props.ico_assess_issue_info;
    return (
      <div>
        <div className="ico-publish">
          <h2 className="ico-publish-title">ICO</h2>
          <table>
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th>ICO</th>
                <th></th>
                <th></th>
                <th></th>
                {/* <th style={{ borderRight: 0 }}>(10)</th> */}
              </tr>
            </thead>
            <tbody>
              {info &&
                info.map(item => {
                  return (
                    <tr key={item.id}>
                      <td>{`${item.crowdfunding_start_at.substring(
                        0,
                        10
                      )}-${item.crowdfunding_end_at.substring(5, 10)}`}</td>
                      <td>{`${item.ico_circulation / 100000000}${unit}`}</td>
                      <td>{`${item.ico_amount / 100000000}${unit}`}</td>
                      <td>{item.ico_accept}</td>
                      <td>{`${item.ico_crowfunding_amount /
                        10000}${item.ico_crowfunding_amount_unit}`}</td>
                      <td>{item.ico_price}</td>
                    </tr>
                  );
                })}
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}



